
import { useState, useEffect } from 'react';
import Header from '../../components/feature/Header';
import Footer from '../../components/feature/Footer';
import Hero from './components/Hero';
import LocationInput from './components/LocationInput';
import NearbyProducts from './components/NearbyProducts';
import NearbyRestaurants from './components/NearbyRestaurants';

export default function Home() {
  const [userLocation, setUserLocation] = useState<{lat: number, lng: number} | null>(null);
  const [isLocationLoading, setIsLocationLoading] = useState(true);

  useEffect(() => {
    // Demander la golocalisation de l'utilisateur avec haute prcision
    if (navigator.geolocation) {
      const options = {
        enableHighAccuracy: true, // Prcision maximale possible
        timeout: 30000, // Augmenter le temps pour une meilleure prcision
        maximumAge: 0 // Ne pas utiliser les donnes mises en cache pour obtenir une localisation rcente
      };

      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude, accuracy } = position.coords;
          console.log(`Localisation dtermine sur la page d'accueil: ${latitude}, ${longitude} - prcision: ${accuracy} mtres`);
          
          setUserLocation({
            lat: latitude,
            lng: longitude
          });
          setIsLocationLoading(false);
        },
        (error) => {
          console.error('Error getting location:', error);
          setIsLocationLoading(false);
          
          // Afficher un message d'erreur appropri  l'utilisateur
          let errorMessage = 'chec de la localisation';
          switch (error.code) {
            case error.PERMISSION_DENIED:
              errorMessage = 'Permission d\'accs  la localisation refuse. Veuillez autoriser l\'accs  la localisation pour afficher les restaurants prs de chez vous.';
              break;
            case error.POSITION_UNAVAILABLE:
              errorMessage = 'Localisation actuellement indisponible. Assurez-vous que le GPS est activ.';
              break;
            case error.TIMEOUT:
              errorMessage = 'Dlai de localisation expir. Vous pouvez saisir votre localisation manuellement.';
              break;
          }
          
          // Peut afficher un message  l'utilisateur ici si ncessaire
          console.warn(errorMessage);
        },
        options
      );
    } else {
      console.warn('Votre navigateur ne prend pas en charge la golocalisation');
      setIsLocationLoading(false);
    }
  }, []);

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <Header />
      
      {/* Hero Section */}
      <Hero />
      
      {/* Location Input Section */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-6">
          <LocationInput 
            userLocation={userLocation}
            isLoading={isLocationLoading}
            onLocationChange={setUserLocation}
          />
        </div>
      </div>

      {/* Nearby Products Section */}
      <div className="max-w-7xl mx-auto px-4 py-12">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold text-gray-900">Produits prs de chez vous</h2>
          <i className="ri-restaurant-2-line text-3xl text-teal-600"></i>
        </div>
        <NearbyProducts userLocation={userLocation} />
      </div>

      {/* Nearby Restaurants Section */}
      <div className="max-w-7xl mx-auto px-4 py-12">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold text-gray-900">Restaurants prs de chez vous</h2>
          <i className="ri-store-2-line text-3xl text-teal-600"></i>
        </div>
        <NearbyRestaurants userLocation={userLocation} />
      </div>

      {/* Footer */}
      <Footer />
    </div>
  );
}
